import { createRouter, createWebHistory } from 'vue-router'
import HomeView from "@/views/HomeView.vue"
import AboutMeView from "@/views/AboutMeView.vue"
import CompetitionTimelineView from "@/views/competition/CompetitionTimelineView.vue"
import NoteView from "@/views/NoteView.vue";
import NotFoundView from '@/views/NotFoundView.vue';
import FoodRecordView from "@/views/FoodRecordView.vue";

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/me/',
    name: 'me',
    component: AboutMeView
  },
  {
    path: '/competition/timeline/',
    name: 'competition_timeline',
    component: CompetitionTimelineView
  },
  {
    path: '/note/',
    name: 'note',
    component: NoteView
  },
  {
    path: '/food_record/',
    name: 'food_record',
    component: FoodRecordView
  },
  {
    path: "/404/",
    name: "not_found",
    component: NotFoundView,
  },
  {
    path: "/:catchAll(.*)",
    redirect: "/404/",
  },

]

const router = createRouter({
  history: createWebHistory(),
  routes
})

export default router
